/** Three container:
 * <div> below is the container of the canvas;
 * optionally, create a canvas here instead of <div> 
 * 
 * @ref attribute gets a reference to the div element
 * sketch will run when the function componentDidMount() 
 * is automatically called by React, when the component is mounted.
 */

import React from 'react';
import SceneController from './SceneController';

export default class Home extends React.Component
{
    constructor()
    {
        super();
        this.sceneController = new SceneController();
    }

    componentDidMount() 
    {
        this.addEventListeners();
        this.sceneController.setup();
        this.update();
    };

    componentWillUnmount() 
    {
        window.removeEventListener('resize', this.onWindowResize);
        document.removeEventListener("mousemove", this.onDocumentMouseMove);
        if(this.sceneController) this.sceneController.dispose();
    };

    addEventListeners()
    {
        window.addEventListener('resize', this.onWindowResize, false);
        document.addEventListener("mousemove", this.onDocumentMouseMove, false);
    };

    onWindowResize = () => 
    {
        if(this.sceneController) 
        {
            this.sceneController.onWindowResize();
            this.sceneController.update();
        }    
    };

    onDocumentMouseMove = (e) =>
    {
        if(this.sceneController) this.sceneController.onMouseMove(e);
    }

    update = () =>
    {
        requestAnimationFrame(this.update);
        if(this.sceneController) this.sceneController.update();
    };

    render () 
    {
        return (
            <div ref={element => this.threeRootElement = element} />
        );
    }
}