import * as THREE from 'three';
import gsap from 'gsap';
import { getRandom } from './utils';

export default class TrioSpheres
{
    constructor(scene, terrainSize)
    {
        this.scene = scene;
        this.terrainSize = terrainSize;
        this.trioMeshes = [];
        this.speed1 = 1;
        this.speed2 = .3;
        this.isAnimating = false;

        this.setup();
    }

    setup()
    {
        this.material = new THREE.MeshStandardMaterial();
        this.luminosityFact = 2.;
        const group2 = new THREE.Object3D();
        
        for(let i=0; i<70; i++) 
        {
            this.material.color.setHSL(getRandom(0, 1), .5, .5);
            const x = getRandom(-this.terrainSize/2, this.terrainSize/2);
            const z = getRandom(-this.terrainSize/2, this.terrainSize/2);
            // const xOffset = getRandom(-this.terrainSize/3, this.terrainSize/3);
		    //const zOffset = getRandom(-this.terrainSize/6, 0);
    
            const size = getRandom(2, 5);
    
            let mesh = new THREE.Mesh(new THREE.SphereBufferGeometry(size, 32, 32), this.material);
            mesh.size = size;
            mesh.position.set(x, size, z);	
            mesh.name = "intersectable";
    
            const small1 = mesh.clone();
            const scale = getRandom(0.2, 0.3);		
            small1.scale.set(scale, scale, scale);
            
            const small2 = small1.clone();
            const group1 = new THREE.Object3D();
            group1.add(mesh);
            group1.add(small1);
            group1.add(small2);
            
            this.scene.add(group1);
            this.scene.add(group2);

            this.trioMeshes.push(group1);
        }  
    };

    rotate(mesh) 
    {
        let tween = gsap
			.to(mesh.rotation, 10, {y: mesh.rotation.y+Math.PI/2, z: mesh.rotation.z+Math.PI/2});
        tween.play();
    };

    // bump(mesh)
    // {
    //     this.isAnimating = true;
    //     const originalPosX = mesh.position.z;
	// 	const originalPosY = mesh.position.y;
	// 	const originalPosZ = mesh.position.z;
	// 	const easing = 'bounce';

	// 	let bump = gsap.timeline();
	// 	bump.to(mesh.position, 1, {x: mesh.position.x + 1.02, y: mesh.position.y + 1.02, z: mesh.position + 1.02, ease:easing})
	// 		.to(mesh.position, 1, {x: originalPosX, y: originalPosY, z: originalPosZ, ease:easing});
		
	// 	bump.eventCallback("onComplete", () =>
	// 	{
	// 		this.isAnimating = false;
	// 	});
    // }
    

    update(time) 
    {
        this.material.color.setHSL(Math.sin(time), 0.5, 0.5);
		const scale = (Math.sin(time)+6)/7;
		const ang1 = time/2;
        const ang2 = time/4;
        time += 0.01;

        for(let i=0; i<this.trioMeshes.length; i++) 
        {
            const sphere = this.trioMeshes[i].children[0];

			const rad = sphere.size;
			const small1 = this.trioMeshes[i].children[1];
            const small2 = this.trioMeshes[i].children[2];

			small1.position.x = sphere.position.x + rad * Math.sin(ang1*this.speed1) * Math.cos(ang2*this.speed1);
			small1.position.y = sphere.position.y + rad * Math.sin(ang1*this.speed1) * Math.sin(ang2*this.speed1);
			small1.position.z = sphere.position.z + rad * Math.cos(ang1*this.speed1);

			small2.position.x = sphere.position.x + rad * Math.sin(-ang1*this.speed2) * Math.cos(-ang2*this.speed2);
			small2.position.y = sphere.position.y + rad * Math.sin(-ang1*this.speed2) * Math.sin(-ang2*this.speed2);
            small2.position.z = sphere.position.z + rad * Math.cos(-ang1*this.speed2);

            sphere.scale.set(scale, scale, scale);
		}
	};
    
    onAction(e)
    {
        for(let i=0; i<this.trioMeshes.length; i++) 
        {
            // this.bump(this.trioMeshes[i].children[0]);
            // this.bump(this.trioMeshes[i].children[1]);
            // this.bump(this.trioMeshes[i].children[2]);
            // this.rotate(this.trioMeshes[i].children[0]);
	        // this.rotate(this.trioMeshes[i].children[1]);
            // this.rotate(this.trioMeshes[i].children[2]);
        }      
    };
}