import React from 'react';
import { Link } from 'react-router-dom';
import history from '../history';
// import OpenSketch from '../OpenSketch';


export default class SketchItem extends React.Component 
{
    constructor(props)
    {
        super(props);
    }

    openSketch()
    {
        // console.log(this.props.sketch.component);
        history.push(this.props.sketch.url);
        return (
            <div>
            <Link to={`/webgl/${this.props.sketch.id}`}>
                {/* <OpenSketch component={ this.props.sketch.component } /> */}
            </Link>
            </div>
        );  
    };

    render() 
    {
        return(
        <div>
            <img className="Sketch-Item"
                src={this.props.sketch.cover}
                alt={this.props.sketch.title}
                onClick={() => this.openSketch()}
            />
            {/* <iframe src={this.props.sketch.url} className="Sketch-Content" /> */}
        </div>
        )
    }
}